<template>
    <div>
        <header>
            <p>查询</p>
        </header>
        <div class="inquiry_wrap">
            <div class="inquiry_list">
                <router-link :to='list.href' v-for='list in dataList' :key='list.value'>
                    <span :class="'inquiry_'+list.value"></span>
                    <p>{{list.name}}</p>
                </router-link>
            </div>
        </div>
        <!-- <foot :show="2"></foot>    -->
    </div>
        
</template>
<style scoped>
    .inquiry_wrap{padding-top: 0.88rem;}
    .inquiry_list{display: flex;flex-direction: row;flex-wrap:wrap;align-items: center;background: #fff;padding: 40px 15px 40px 15px;}
    .inquiry_list a{display: inline-block;text-align: center;width: 25%;}
    .inquiry_list p{font-size: 0.28rem;color:#1a1a1a;}

    .inquiry_list a>span{display: block;height: 50px;margin-bottom: 10px;}
    .inquiry_policy{background: url(../../../static/images/info_policy.png) no-repeat center;background-size: contain;}
    .inquiry_guide{background: url(../../../static/images/info_guide.png) no-repeat center;background-size: contain;}
    .inquiry_question{background: url(../../../static/images/info_ques.png) no-repeat center;background-size: contain;}
</style>
<script>
    import f from '../../components/footer.vue'
    export default{
        data(){
            return {
                dataList:[
                    {name:'录取情况',value:'',href:''},
                    {name:'学位房查询',value:'',href:''},
                    {name:'申请状态',value:'',href:''},
                    {name:'审核状态',value:'',href:''},
                ]
            }
        },
        components: {
            'foot': f // 添加底部导航组件
        },
    }
</script>
<style scoped>
    .inquiry_wrap{padding-top: .88rem;background: #fff;}
</style>


